import Group from '../shapes/group';
import Point from '../geometry/point';
import SurfaceFactory from './surface-factory';
import Observable from './observable';
import elementStyles from '../common/element-styles';
import elementSize from '../common/element-size';
import elementOffset from '../common/element-offset';

const events = [
    "click",
    "mouseenter",
    "mouseleave",
    "mousemove",
    "resize",
    "tooltipOpen",
    "tooltipClose"
];

class Surface extends Observable {

    constructor(element, options) {
        super();

        this.options = Object.assign({}, options);
        this.element = element;

        this._click = this._handler("click");
        this._mouseenter = this._handler("mouseenter");
        this._mouseleave = this._handler("mouseleave");
        this._mousemove = this._handler("mousemove");

        this._visual = new Group();

        elementSize(element, this.options);

        this.bind(events, options);

        this._enableTracking();
    }

    draw(element) {
        this._visual.children.push(element);
    }

    clear() {
        this._visual.children = [];
    }

    destroy() {
        this._visual = null;
        this.unbind();
    }

    eventTarget(e) {
        let domNode = e.target;
        let node;

        while (!node && domNode) {
            node = domNode._kendoNode;
            if (domNode === this.element) {
                break;
            }

            domNode = domNode.parentElement;
        }

        if (node) {
            return node.srcElement;
        }
    }

    exportVisual() {
        return this._visual;
    }

    getSize() {
        return elementSize(this.element);
    }

    setSize(size) {
        elementSize(this.element, size);

        this._size = size;
        this._resize();
    }

    resize(force) {
        const size = this.getSize();
        const currentSize = this._size;

        if (force || (size.width > 0 || size.height > 0) && (!currentSize || size.width !== currentSize.width || size.height !== currentSize.height)) {
            this._size = size;
            this._resize(size, force);
            this.trigger("resize", size);
        }
    }

    size(size) {
        if (!size) {
            return this.getSize();
        }

        this.setSize(size);
    }

    suspendTracking() {
        this._suspendedTracking = true;
    }

    resumeTracking() {
        this._suspendedTracking = false;
    }

    _enableTracking() {}

    _resize() {}

    _handler(event) {
        return (e) => {
            const node = this.eventTarget(e);
            if (node && !this._suspendedTracking) {
                this.trigger(event, {
                    element: node,
                    originalEvent: e,
                    type: event
                });
            }
        };
    }

    _elementOffset() {
        const element = this.element;
        const { paddingLeft, paddingTop } = elementStyles(element, [ "paddingLeft", "paddingTop" ]);
        const { left, top } = elementOffset(element);

        return {
            left: left + parseInt(paddingLeft, 10),
            top: top + parseInt(paddingTop, 10)
        };
    }

    _surfacePoint(event) {
        const offset = this._elementOffset();
        const coord = eventCoordinates(event);
        const x = coord.x - offset.left;
        const y = coord.y - offset.top;

        return new Point(x, y);
    }
}

Surface.create = function(element, options) {
    return SurfaceFactory.current.create(element, options);
};

function eventCoordinates(event) {
    return {
        x: event.pageX || event.clientX || 0,
        y: event.pageY || event.clientY || 0
    };
}

export default Surface;


